*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  --dark-blue: hsl(200, 100%, 15%);
  --lt-gray: #c0c0c0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.font-mono {
  font-family: monospace;
}

.separator {
  margin: 0 20px;
  color: #808080;
}

#app {
  width: 800px;
  margin: 20px auto;
}

#app > div {
  /*border: 1px solid #c0c0c0;*/
}

#app h2 {
  margin: 0;
  padding: 10px 0;
  font-size: 1rem;
  color: var(--dark-blue);
  border-bottom: 1px solid var(--lt-gray);
}

#app h3 {
  margin: 0;
}

#search {
  margin: 20px 0;
  padding: 20px;
  background: #f0f0f0;
}

#search form {
  display: flex;
  justify-content: space-between;
}

#search input {
  width: 100%;
}

#history table {
  border-collapse: collapse;
}

#history tr:hover {
  background: #f0f0f0;
}

#history td {
  padding: 5px 10px;
}

#display-search-results header,
#history header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 30px;
  margin-bottom: 10px;
  padding: 10px 0 5px;
  border-bottom: 1px solid var(--lt-gray);
}

#display-search-results ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

#display-search-results li {
  /*border-bottom: 1px solid #e0e0e0;*/
}

#display-search-results li a {
  display: flex;
  padding: 15px;
  color: indigo;
  white-space: nowrap;
  text-overflow: ellipsis;
}

#display-search-results li:hover {
  background: #f0f0f0;
}

#tabs {
  display: flex;
  justify-content: space-evenly;
}

#tabs h1 {
  width: 100%;
  margin: 0;
  text-align: center;
  font-size: 1.5rem;
}

#tabs h1 a {
  display: inline-block;
  width: 100%;
  padding: 10px 20px;
  color: #303030;
  background: #f0f0f0;
  text-decoration: none;
}

#tabs h1 a.active {
  color: #f0f0f0;
  background: #303030;
}
